@OptIn(ExperimentalComposeWebApi::class)
@Page
@Composable
fun HomePage() {

    var isDirectionTheSame: MutableState<Boolean?> = remember { mutableStateOf(null) }
    var isCorruptionHide : MutableState<Boolean?> = remember { mutableStateOf(null) }
    var depthValue by remember { mutableStateOf(0) }

    Column(
        modifier = Modifier.fillMaxSize()
            .padding(10.px)
            .background(color = Colors.DarkGrey),
        horizontalAlignment = Alignment.CenterHorizontally,
    ) {

        Box(
            modifier = Modifier.width(400.px)
                .padding(20.px)
                .background(color = Colors.LightGrey)
                .borderRadius(16.px)
                .margin(bottom = 10.px),
        ) {
            Text("    ,       .")
            Spacer()
            Text("        !")
        }

        Box(
            modifier = Modifier.width(400.px)
                .padding(20.px)
                .background(color = Colors.LightGrey)
                .borderRadius(16.px)
                .margin(bottom = 10.px),
        ) {

            Column {
                Text("  ")

                Row {
                    NumberInput(
                        min = 0,
                        max = 250,
                    ) {
                        defaultValue(depthValue)
                        onChange { event ->
                            event.value?.let { depthValue = it.toInt() }
                        }
                    }
                    Text("")

//                    (
//                            type = InputType.Number,
//                    ) {
//                    defaultValue(depthValue)
//                    onChange { event ->
//                        event.value?.let { depthValue = it.toInt() }
//                    }
//                }
                }
            }
        }

        when {
            depthValue == 0 -> {
                Dialog("    ?", isCorruptionHide)
                isCorruptionHide.value?.let {
                    if(it) EndoTipBox()
                    else VisualTipBox()
                }
            }
            depthValue < 6 -> {
                MagnetTipBox()
            }
            depthValue < 25 -> {
                InductTipBox()
            }
            depthValue < 100 -> {
                Dialog("        ?", isDirectionTheSame)
                isDirectionTheSame.value?.let {
                    if (it) UltraTipBox(true)
                    else RadicTipBox()
                }
            }
            depthValue < 250 -> {
                UltraTipBox(false)
            }

            else -> {
                TipBox(" ,    - ")
            }
        }

//        PictureTip("visual/1.png", " -        ")
    }
}

@Composable
fun PictureTipBox(
    src : String,
    description: String,
) {
    Box(
        modifier = Modifier.width(400.px)
            .padding(20.px)
            .margin(10.px)
            .borderRadius(16.px)
            .background(color = Colors.LightGray)
    ) {
        Image(
            src = src,
            modifier = Modifier.align(Alignment.TopCenter)
                .maxSize(350.px)
                .borderRadius(16.px)
        )
        Text(description)
    }
}

